.player-list{
    margin-bottom: 0;
        padding: 0 10px 0 48px;
        list-style-type: decimal-leading-zero;
}
.player{
    font-size: 25px;
    letter-spacing: 1px;
    font-weight: 600;
    height: 42px;   
    margin-bottom: 5px;
}
.player:not(:last-child){
    margin-bottom: 30px;
}
.player__name{
    display: inline-block;
    width: 50%;
    height: 42px;
}
.player__delete{
    float: right;
     min-width: 15%;
     text-align: right;
     font-size: 30px;
    padding: 3px;
    cursor: pointer;
}
.player__delete:hover{
    color: #ff3f34;
}