 .show-container{
     position: fixed;
     top: 75px;
     transition: all 500ms;
     bottom: 0;
     left: 0;
     right: 0;
     background-color: rgba(0, 0, 0, 0.644);
 }
 .show-role{
        position: fixed;
        z-index: 200;
        top:calc(50% + 42.5px);
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #fff;
        border: 3px solid rgb(41, 40, 40);
        width: 300px;
        height: 400px;
        animation: show 500ms 1 ease-in-out;
    }
    @keyframes show{
        0%{
            top: 100%;
            left: 50%;
            transform: translate(-30%, -30%);
        }
        100%{
           transform: translate(-50%,-50%);
        }
    }
    .show-card{
        width: 100%;
        height: 100%;
    }
    .card-name{
        height: 60px;
        position: absolute;
        background: #ff3f34;
        bottom:0px;
        right: 90px;
        left: 0;
        color: #fff;
        padding: 15px;
        margin: 0;
        text-align: center;
    }
    .btn-ok{
        height: 60px;
        width: 90px;
        display: inline-block;
        position: absolute;
        z-index: 20;
        background: black;
        bottom:0px;
        color: #fff;
        right: 0;
        padding: 10px 30px;
        font-size: 35px;
        cursor: pointer;
        font-weight: bold;
        letter-spacing: 1px;
    }