.btn-wrapper{
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-start{
    width: 100%;
    display: inline-block;
    font-size: 25px;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: 1px;
    border: 3px solid #ff3f34;
    text-align: center;
        padding: 10px 0;
    color: black;
    margin: 12px 0 10px;
}
.btn-start:hover{
    background-color: #ff3f34;
    color: #fff;
}