.timer-wrapper{
    border-bottom: 1px dashed #ccc;
    padding-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.timer{
    width: 120px;
    height: 120px;
padding: 10px;
border-radius: 100%;
border: 4px solid #ccc;
color: #444;
display: flex;
align-items: center;
justify-content: center;
font-size: 50px;
margin: -40px auto 0;
}
.btns-timer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.btns-timer{
    border-bottom: 1px dashed #ccc;
    padding: 30px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}
.btns-timer button{
    min-width: calc(33.33% - 20px );
    padding: 10px 15px;
    font-size: 18px;
    font-family: inherit;
    color: #333;
    cursor: pointer;
}
.title-timer{
    padding: 25px 15px 0;
    text-align: center;
    font-size: 27px;
    color: #333;
}
.day-btn-wrapper{
    padding: 40px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px dashed #ccc;
    margin-bottom: 20px;
}
.reset-day-btn{
    width: 100%;
    height: 50px;
    font-size: 25px;
    font-family: inherit;
    color: #ff3f34;
    border: 2px solid #ff3f34;
    background: #fff;
    cursor: pointer;
}
.reset-day-btn:hover{
      color: #fff;
       background: #ff3f34;
}