.players-dead{
        display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
    gap: 0 25px;
}
.player-dead{
    height: 80px;
    position: relative;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 1px;
    outline: none;
    background-color:#fff ;
    width: calc(25% - 20px);
    min-width: 300px;
    flex-shrink: 0;
    padding: 10px;
    border-radius: 5px;
    margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    }
.player-dead:hover{
  box-shadow: 1px 1px 8px rgba(0,0,0,0.25),1px 1px 8px rgba(0,0,0,0.25);
    }
