 .dead-container{
     position: fixed;
     top: 75px;
     transition: all 500ms;
     bottom: 0;
     left: 0;
     right: 0;
     background-color: rgba(0, 0, 0, 0.644);
 }
 .dead-role{
        position: fixed;
        z-index: 200;
        top:calc(50% + 42.5px);
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #fff;
        border: 3px solid rgb(41, 40, 40);
        width: 300px;
        height: 400px;
        animation: show 500ms 1 ease-in-out;
    }
    @keyframes show{
        0%{
            top: 100%;
            left: 50%;
            transform: translate(-30%, -30%);
        }
        100%{
           transform: translate(-50%,-50%);
        }
    }
    .dead-name{
        height: 60px;
        position: absolute;
        background: #ff3f34;
        bottom:0px;
        right: 110px;
        left: 0;
        color: #fff;
        padding: 0px 15px 0px;
        line-height: 60px;
        margin: 0;
        text-align: center;
        font-size: 20px;
    }
    .btn-dead{
        height: 60px;
        width: 120px;
        display: inline-block;
        position: absolute;
        z-index: 20;
        background: black;
        bottom:0px;
        color: #fff;
        right: 0;
        padding: 13px 30px 10px;
        font-size: 30px;
        cursor: pointer;
        font-weight: bold;
        letter-spacing: 1px;
    }
    .dead-card{
        width: 100%;
    }
    .ok-btn-dead{
        color: #fff;
        position: absolute;
        top: 10px;
        left: 15px;
        font-size: 35px;
        cursor: pointer;
    }
    .dead-badge{
        transform: rotate(35deg);
        color: #fff;
        background: red;
        padding: 5px;
        position: absolute;
        right: 30px;
        font-weight: bold;
        letter-spacing: 1px;
        font-size: 20px;
    }