@import url('https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300&display=swap');
*,*::before,*::after{
    box-sizing: inherit;
}
html{
    box-sizing: border-box;
}
body{
    padding: 0;
    margin: 0;
     font-family: 'Londrina Solid', cursive;
      letter-spacing: 3px;
      padding-top: 150px;
}
h1{
    margin: 0;
}
.top-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}
.help{
    text-align: center;
}
.alert-text{
    font-size: 30px;
    letter-spacing: 1px;
    margin: 0;
    margin-top: 30px;
}
.manage-wrapper{
        padding: 0px 20px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.manage-btn{
    display: inline-block;
    width: 100%;
    text-align: center;
    padding: 12px;
    display: inline-block;
    border: 3px solid #ff3f34;
    color: #ff3f34;
    font-size: 25px;
    font-weight: bold;
    letter-spacing: 2px;
    text-decoration: none;
    transition: 200ms ease-in-out;
    margin:  15px 0;
}
.manage-btn:hover{
    color: #fff;
    background: #ff3f34;
}
.reset-game-wrapper{
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.reset-game{
    font-size: 25px;
    width: 100%;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: center;
    border: 3px solid #d3241b;
    text-decoration: none;
    color: #fff;
    background-color: #ff3f34;
    padding: 10px;
}
.reset-game:hover{
    background-color: #fff;
    color: #d3241b;
}