.top-aside{
    padding: 15px 10px 5px;
    background-color: #fff;
    position: fixed;
    top: 75px;
    left: 0;
    right: 0;
}
.add-form{
    display: flex;
    align-items: center;
}
.add-from__input{
    width: 65%;
    height: 50px;
    border: 2px solid #ccc;
    outline: none;
    border-right: none;
    font-size: 20px;
    padding: 0 10px;
}
.add-from__input:focus{
   border: 2px solid #111;
   border-right: none;
}
.add-form__btn{
    width: 35%;
    height: 50px;
    border: none;
    font-family: inherit;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
    background-color: #111;
    color: #fff;
    outline: none;
}
.add-form__btn:hover{
    background-color:  #ff3f34;
}